<template>
	<div class="bigbox">
		<div class="box">
      <mt-header title="预约信息录入">
      	<router-link to="/home" slot="left">
      		<mt-button icon="back">返回</mt-button>
      	</router-link>
      </mt-header>
			<div class="boxs topBox">
				<mt-field label="姓名" placeholder="请输入姓名" v-model="username"></mt-field>
				<mt-field label="身份证号" placeholder="请输入身份证号码" v-model="idcard"></mt-field>
        <mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
			</div>
			<div class="boxs">
				<mt-field label="民族" placeholder="请输入民族" v-model="nation"></mt-field>
				<mt-field label="年龄" placeholder="请输入年龄" type="number" v-model="age"></mt-field>
				<mt-field label="性别" placeholder="请选择性别" v-model="gender"></mt-field>
				<a class="mint-cell mint-field select">
					<mt-radio v-model="gender" :options="['男', '女']"></mt-radio>
				</a>
				<mt-field label="文化程度" placeholder="请选择文化程度" v-model="culture"></mt-field>
				<a class="mint-cell mint-field select">
					<mt-radio v-model="culture" :options="['大专以下', '大专本科']"></mt-radio>
				</a>
				<a class="mint-cell mint-field select">
					<mt-radio v-model="culture" :options="['本科以上']"></mt-radio>
				</a>
			</div>
			<div class="boxs">
				<mt-field label="单位名称" placeholder="请输入单位名称" v-model="unitname"></mt-field>
				<mt-field label="单位性质" placeholder="请选择单位性质" v-model="industry"></mt-field>
				<a class="mint-cell mint-field select">
					<mt-radio v-model="industry" :options="['全民', '集体']"></mt-radio>
				</a>
				<a class="mint-cell mint-field select">
					<mt-radio v-model="industry" :options="['合资','个体']"></mt-radio>
				</a>
				<mt-field label="工种" placeholder="请输入工种(非必填)" v-model="work"></mt-field>
				<mt-field label="工龄" placeholder="请输入工龄(非必填)" type="number" v-model="workingYears"></mt-field>
			</div>


			<div class="check-boxs">
				<!-- <img v-if="checkShows" src="@/assets/isCheck.png" @click="check"></img>
				<img v-else src="@/assets/check.png" @click="check"></img>
				<div class="check-text">
					<div>下一步需同意</div>
					<router-link class="link" to="/text">《信息录入授权说明》</router-link>
				</div> -->
			</div>
			<div class="check-box">
				<div class="appoint" @click="appoint">预约</div>
			</div>
		</div>
	</div>
</template>
<script>
const axios = require('axios')
  import { Indicator,Toast } from 'mint-ui'
export default {
  data: function () {
    return {
      selectValue: '',
      checkShows: true,
      username: '',
      idcard: '',
      nation: '',
      phone: '',
      work: '',
      workingYears: '',
      unitname: '',
      identifier: '',
      industry: '全民',
      receipt: '',
      checks: false,
      gender: '男',
      age: '',
      selected: '1',
      culture: '大专以下', // 文化程度
      actions: [{ name: '拍照',
        method: function () {
          console.log('1')
        } }, { name: '从相册选取',
        method: function () {
          console.log('2')
        } }]
    }
  },
  methods: {
    check () {
      if (this.checkShows == false) {
        this.checkShows = true
      } else {
        this.checkShows = false
      }
    },
    appoint () {
      if(this.username == ''){
        Toast({
          message: '请填写姓名',
          position: 'middle',
          duration: 3000
        })
      }else if(this.idcard.length != 18){
        Toast({
          message: '请核对身份证号码',
          position: 'middle',
          duration: 3000
        })
      }else if(this.phone.length != 11){
        Toast({
          message: '请核对手机号码',
          position: 'middle',
          duration: 3000
        })
      }else if(this.nation==''){
        Toast({
          message: '请输入民族',
          position: 'middle',
          duration: 3000
        })
      }else if(this.age==''){
        Toast({
          message: '请输入年龄',
          position: 'middle',
          duration: 3000
        })
      }else if(this.unitname==''){
        Toast({
          message: '请输入单位名称',
          position: 'middle',
          duration: 3000
        })
      }else{
      Indicator.open('加载中...')
        axios({
          method: 'post',
          url: 'https://www.c65z45.cn/api/Reserve/Reserve',
          data: {
            openID: '',
            HospitalID: this.selectValue.HospitalID,
            DateID: this.selectValue.ID,
            PictureID: '',
            Date: this.selectValue.Date,
            Name: this.username,
            IDCardNo: this.idcard,
            Sex: this.gender,
            Age: this.age,
            Nation: this.nation,
            Schooling: this.culture,
            UnitProperty: this.industry,
            UnitName: this.unitname,
            WorkType: this.work,
            WorkTime: this.workingYears,
            PhoneNo: this.phone,
            Token: 'B68DE9D1F8584D7099BCE47144D0351C'
          }
        }).then(res => {
          Indicator.close()
          if(res.data.IsSuccess){
            this.$router.push({
            	path: '/result',
            	query: {Result:res.data}
            })
          }else{
            Toast({
              message: res.data.Message,
              position: 'middle',
              duration: 3000
            })
          }
        }).catch(error => {
          Indicator.close()
          Toast({
            message: '服务器错误',
            position: 'middle',
            duration: 3000
          })
        })
      }
    }
  },
  created(){
    this.selectValue = this.$route.query.selectValue
  }

}
</script>

<style>
	.bigbox {
		width: 100%;
		height: 100%;
		overflow-y: auto;
	}

	.box {
		background: rgb(248, 248, 248);
    position: relative;
	}

	.mint-header {
		background: rgb(0, 204, 153);
    width: 100%;
    position: fixed;
    top: 0;
    z-index: 99;
	}
  .topBox{
    padding-top: 0.7rem;
  }
	.boxs {
		margin-top: 0.5rem;
	}

	.boxs:nth-child(1) {
		margin-top: 0;
	}

	.appoint {
		margin: 0.3rem auto 0 auto;
		width: 95%;
		background: rgb(0, 204, 153);
		color: #fff;
		text-align: center;
		line-height: 1rem;
		border-radius: 0.1rem;
		font-size: 0.35rem;
		font-weight: 600;
	}

	.mint-badge {
		height: 0.5rem;
		width: 2rem;
		display: block;
		line-height: 0.5rem;
	}

	.check-box {
		position: fixed;
		bottom: 0rem;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		align-items: center;
		width: 100%;
	}

	.select>div {
		width: 100%;
		display: flex;
		padding-left: 1.5rem;
	}
	.check-boxs {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: -0.05rem;
		padding: 0.3rem 0rem 2rem 0rem;
	}

	.check-boxs image {
		width: 0.34rem;
		height: 0.3rem;
	}

	.check-boxs .check-text {
		display: flex;
		font-size: 0.23rem;
		margin-left: 0.1rem;
	}

	.check-boxs .check-text .link {
		color: #5A7DFF;
	}

	.mint-radiolist-label {
		padding: 0px;
	}
	.bot-select{
		width: 7.5rem;
	}
</style>
